import LayoutNone from '../../layouts/LayoutNone/LayoutNone.jsx'

const Login = (props) => {
	const sendLogin = () => {
		const userId = (Math.random() + '').slice(2);
		alert(`登录成功:${userId}`);
		sessionStorage.setItem('userId', userId);
		props.history.push({pathname:"/home"});
	}
	return (
		<LayoutNone>
			<button onClick={sendLogin}>模拟登录</button>
		</LayoutNone>
	);
};

export default Login;
